			<view slot="content">地址列表</view>
		</cu-custom>
		<view class="padding-lr bg-white">
			<view class="solid-bottom padding-tb" v-for="(item, index) in list" :key="index" @click="onGoDetail(item)">
				<view class="username margin-bottom-xs">
					张三<text class="margin-left text-gray">15325214125</text>
				</view>
				<text class="text-grey text-sm">
					北京市顺义区大鼓镇
				</text>
			</view>
		</view>
		<!-- 新增 -->
		<button class="fixed bottom" type="primary" @click="onGoDetail(null)">新增</button>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				list: new Array(10).fill(10)
			}
		},
		methods: {
			onGoDetail(item) {
				console.log('item', item);
				let url = `/pages/my/settings/address`;
				url += item ? '?id=' + item : '';
				uni.navigateTo({
